import mpvueCityPicker from "@/components/uni-ui/mpvue-citypicker/mpvueCityPicker.vue"
import {mapActions} from "vuex"
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			isedit:false,
			themeColor: '#007AFF',
			pickerValue: [24, 0, 0],
			index:-1,
			form:{
				path:'',
				province:"",
				city:"",
				district:"",
				address:"",
				name:"",
				phone:"",
				zip:"",
				detailPath:"",
				default:0
			}
		}
	},
	onLoad(e) {
		if (e.data) {
			let result = JSON.parse(e.data)
			this.form = result.item
			this.index = result.index
			this.isedit = true
			uni.setNavigationBarTitle({
				title:"修改收货地址"
			})
		}
	},
	// 监听返回
	onBackPress() {
	  if (this.$refs.mpvueCityPicker.showPicker) {
	  	this.$refs.mpvueCityPicker.pickerCancel();
	    return true;
	  }
	},
	// 页面卸载
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	},
	computed:{
		path(){
			if(this.form.province){
				return this.form.province+'-'+this.form.city+'-'+this.form.district
			}	
		}
	},
	methods: {
		...mapActions(['updatePathAction','createPathAction']),
		// 提交
		submit(){
			// 验证表单
			
			// 修改
			if (this.isedit) {
				this.$H.post('/useraddresses/'+this.form.id,this.form,{
					token:true
				}).then(res=>{
					this.updatePathAction({
						index:this.index,
						item:this.form
					})
					uni.showToast({ title: '修改成功' });
					uni.navigateBack({ delta: 1 })
					uni.$emit('updateUserPathList')
				})
				return;
			}
			
			
			
			this.$H.post('/useraddresses',this.form,{
				toekn:true
			}).then(res=>{
				// 创建
				this.createPathAction(this.form)
				uni.showToast({ title: '创建成功' });
				uni.navigateBack({ delta: 1 });
			})
			
		},
		// 三级联动确定
		onConfirm(e) {
			//this.form.path = e.label
			let arr = e.label.split('-')
			console.log(e)
			this.form.province = arr[0]
			this.form.city = arr[1]
			this.form.district = arr[2]
			this.pickerValue = e.value
		},
		// 显示三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
	}
}
